<template>
  <div class="ml-16 mt-6">
    <div v-for="(reply, i) in getReplies" :key="i" light>
      <!-- Single Reply -->
      <single-reply :reply="reply" />
    </div>

    <v-btn
      v-if="getReplies.length !== replies.length"
      color="primary"
      text
      small
      @click="limit += 5"
      >{{ $t('replies.showMoreReplies') }}</v-btn
    >
    <v-btn
      v-if="getReplies.length > getRepliesLength"
      color="primary"
      text
      small
      @click="limit = 5"
      >{{ $t('replies.showLessReplies') }}</v-btn
    >
    <v-btn
      v-if="replies.length !== getReplies.length"
      color="primary"
      text
      small
      @click="limit = replies.length"
      >{{ $t('replies.showAllReplies') }}</v-btn
    >
  </div>
</template>

<script>
export default {
  components: {
    SingleReply: () => import('@/components/blog/replies/SingleReply')
  },
  props: {
    replies: {
      type: [Array],
      default: () => []
    }
  },
  data() {
    return {
      limit: this.$store.state.settings.settings.blog.comments.repliesLimit
    }
  },
  computed: {
    getRepliesLength() {
      return this.$store.state.settings.settings.blog.comments.repliesLimit
    },
    getReplies() {
      return this.replies.slice(0, this.limit)
    }
  }
}
</script>

<style lang="scss" scoped></style>
